function spotifyLogout() {
  const spotifyLogoutWindow = window.open('https://accounts.spotify.com/en/logout',
    'Spotify Logout', 'width=700,height=500,top=40,left=40');
  setTimeout(function () { spotifyLogoutWindow.close() }, 2000);
}

import * as htmlToImage from "html-to-image";

const elems = ['.capture']

elems.forEach((elem, ind) => {
    const node = document.querySelector(elem)
 
    htmlToImage.toPng(node)
        .then(function (dataUrl) {
            let img = new Image();
            img.src = dataUrl;
            document.body.appendChild(img);
        })
        .catch(function (error) {
            console.error('oops, something went wrong!');
            console.log(error)
        });
});

// Added event listener to "downloadButton"
document.getElementById('downloadButton').addEventListener('click', () => {
  htmlToImage.toPng(document.getElementById('capture'))
    .then(function (dataUrl) {
      let link = document.createElement('a');
      link.download = 'screenshotButton.png';
      link.href = dataUrl;
      link.click();
    });
});

